/* modulo 42 - pagina de Checkout - criando o componente de dados  de pagamento*/
import React, { Component } from 'react';

import FormRadio from '../../components/Inputs/FormRadio';

class DadosPagamento extends Component {

	renderOpcoesPagamento() {
		
		return (
			<div className='flex horizontal'>
				<div className='flex-1'>
					<FormRadio name='tipo_pagamento_selecionado' checked={true} label='Boleto Bancário' />
				</div>
				<div className='flex-1'>
					<FormRadio name='tipo_pagamento_selecionado' checked={false} label='Cartão de Crédito' />
				</div>
			</div>
		);

	}

	render() {
		return (
			<div className='Dados-Frete'>
				<h2>DADOS DE PAGAMENTO</h2>
				<br />
				{this.renderOpcoesPagamento()}
				<br />
				{this.renderPagamentoComBoleto()}
				{this.renderPagamentoComCartao()}
			</div>
		);
	}
}

export default DadosPagamento;